* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

body {
  background-color: #0c2646;
  color: white;
}

.container {
  margin: 0 auto;
}

img {
  width: 100%;
  height: 100%;
}

.c-cell {
  height: 17rem;
  width: 80%;
  background-color: white;
  color: #0c2646;
}

.c-flag {
  height: 130px;
}

.myrow {
  width: 60%;
  margin: 0 auto;
  border: rgba(255, 255, 255, 0.5) 1px solid;
  padding: 20px;
}

.gallery-cell {
  width: 90%;
  counter-increment: gallery-cell;
}

.margin-top-20 {
  margin-top: 20;
}

.w-65 {
  width: 65%;
}

.card {
  font-size: 1.875rem;
}

@media only screen and (min-width: 600px) {
  /* for tablets */
  .gallery-cell {
    width: 50%;
  }
}
@media only screen and (min-width: 768px) {
  .gallery-cell {
    width: 25%;
  }
}